function sendDataToServer(survey) {
    //survey.sendResult('398b1917-1d7b-4c0f-b910-0deb09e4c1e3');
    console.log("Survey results: " + JSON.stringify(survey.data["question1"]));
    var riskscore = 0;
    console.log("Survey results: " + JSON.stringify(survey.data["question1"]));
    var data = survey.data;

    db.collection("Patients ACS Symptom Checklist").add({
        Patient_Name: JSON.stringify(survey.data["question16"]),
        Chest_Pressure: JSON.stringify(survey.data["question1"]),
        Shoulder_Pain: JSON.stringify(survey.data["question2"]),
        Sweating: JSON.stringify(survey.data["question3"]),
        Palpitations: JSON.stringify(survey.data["question4"]),
        Chest_Discomfort: JSON.stringify(survey.data["question5"]),
        Upper_Back_Pain: JSON.stringify(survey.data["question6"]),
        Short_of_Breath: JSON.stringify(survey.data["question7"]),
        Arm_Pain: JSON.stringify(survey.data["question8"]),
        Unusual_Fatigue: JSON.stringify(survey.data["question9"]),
        Nausea: JSON.stringify(survey.data["question10"]),
        Lightheaded: JSON.stringify(survey.data["question11"]),
        Chest_Pain: JSON.stringify(survey.data["question12"]),
        Indigestion: JSON.stringify(survey.data["question13"]),
        Chief_Complaint: JSON.stringify(survey.data["question17"]),
        Other: JSON.stringify(survey.data["question14"]),
        Risk_Score: JSON.stringify(survey.data["risk score"])
    })
    .then(() => {
        console.log("Document successfully written!");
    })
    .catch((error) => {
        console.error("Error writing document: ", error);
    });

}


